.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
}

.Appmain {
  padding-top: 5vh;
  height: 70vh;
  width: 100%;
  border: 1px solid lightgrey;
  background-color: #e1eef6;
}

.Footer {
  text-align: left;
  border: 1px solid lightgrey;
  height: 20vh;
}

.myModal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.modal_overlay {
  background-color: rgba(0, 0, 0, 0.411);
  width: 100%;
  height: 100%;
}

.modal_content {
  width: 30%;
  height: 7vh;
  display: inline-block;
  background-color: white;
  margin-top: 30vh;
  padding-top: 3vh;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  color: black;
  vertical-align: middle;
  position: relative;
}

.displayMyModal {
  display: none;
}

#checkbox {
  margin-top: 5px;
}

#box {
  padding-bottom: 2px;
}

#x {
  position: absolute;
  top: 1px;
  right: 5px;
}

#x:hover {
  cursor: pointer;
}

.Menu {
  text-align: center;
  height: 7vh;
  background-color: #fcbe32;

}

.Menus {
  margin: 1.5vh 10px;
  width: 115px;
  line-height: 4vh; 
  display: inline-block;
  transition: all .2s ease-in;
  font-weight: bold;
  border-bottom: 2px solid rgba(255, 255, 255, 0);
  letter-spacing: 2px;
}

a {
  text-decoration: none;
  color: inherit;
}

.Menus:hover {
  letter-spacing: 6px;
  border-bottom: 2px solid #ff5f2e;
}

.lightmode {
  background: white;
  color: black;
  transition: all .2s ease-in;
}

.darkmode {
  background-color: rgba(0, 0, 0, 0.822);
  color: white;
  transition: all .2s ease-in;
}

.Home {
  text-align: center;
  font-size: 65px;
  font-weight: lighter;
  font-family: 'Do Hyeon', sans-serif;
  /* font-family: 'East Sea Dokdo', cursive; */
  line-height: 95px;
}

@media screen and (max-width: 600px),(max-height: 600px) { 
  .Menus { 
    padding-top: 0px;
    width: 80px;
    font-weight: normal;
    font-size: medium;
    letter-spacing: 0px;
  }
  .Menus:hover {
    letter-spacing: 2px;
    border-bottom: 1px solid #ff5f2e;
  }
  .modal_content {
    font-size: medium;
  }
}